<template>
  <section class="program">
    <div class="container">
      <div class="program__inner">
        <div class="program__title">
          <h2>{{ title }}</h2>
        </div>

        <div class="program__picture-wrapper">
          <swiper
            class="swiper program__swiper"
            ref="swiper"
            :options="SliderOptions"
          >
            <swiper-slide
              v-for="(slide, b) in slides"
              :key="b"
              class="program__item"
            >
              <div class="program__texts">
                <p class="program__day">{{ slide.day }}</p>
                <span class="program__date"> {{ slide.date }} </span>
                <div
                  class="program__info-item"
                  v-for="(item, i) in slide.paragraphs"
                  :key="i"
                >
                  <p>{{ item.title }}</p>
                  <span>{{ item.text }}</span>
                </div>
              </div>
            </swiper-slide>
            <div class="swiper-pagination " slot="pagination"></div>
          </swiper>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'program',
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      title: ' ',
      SliderOptions: {
        breakpoints: {
          0: {
            slidesPerView: 1,
            spaceBetween: 20,
            allowTouchMove: true,
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 30,
            allowTouchMove: true,
          },
          1024: {
            slidesPerView: 3,
            spaceBetween: 28,
            allowTouchMove: false,
          },
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },

      slides: [
        {
          day: '1 ',
          date: '22 , , 20:00',
          paragraphs: [
            {
              title: '    ',
              text:
                ',     ,    ',
            },
            {
              title: '   3   ',
              text:
                ',         .   ',
            },
            {
              title: '   100   ',
              text:
                '     ,    ',
            },
          ],
        },
        {
          day: '2 ',
          date: '23 , , 20:00',
          paragraphs: [
            {
              title: '  ',
              text:
                ',        ',
            },
            {
              title: '  ',
              text:
                ',         ,   ',
            },
            {
              title: '   ',
              text:
                '   ,       ',
            },
          ],
        },
        {
          day: '3 ',
          date: '24 , , 20:00',
          paragraphs: [
            {
              title: '  ',
              text:
                '       ',
            },
            {
              title: '   ',
              text:
                '     -       ',
            },
            {
              title: '     ',
              text:
                ' ,         ',
            },
          ],
        },
      ],
    }
  },
}
</script>

