<template>
  <section class="banners">
    <div class="container">
      <div class="banners__inner">
        <div class="banners__texts">
          <p class="banners__date">
            <svg class="icon" width="31" height="31" aria-hidden="true">
              <use xlink:href="#earth"></use>
            </svg>

            {{ date }}
          </p>
          <h1 class="banners__page-title" v-html="title"></h1>
          <p class="banners__subtitle" v-html="subtitle"></p>
          <ul class="banners__list">
            <li class="banners__item" v-for="(item, i) in advantages" :key="i">
              <svg class="icon" width="23" height="23" aria-hidden="true">
                <use xlink:href="#star"></use>
              </svg>
              <p>{{ item.text }}</p>
            </li>
          </ul>
          <p class="banners__offer">{{ offer }}</p>
          <button class="button button--join" @click="goTo(link)">
            {{ button }}
          </button>
          <p class="banners__speaker">{{ speaker }}</p>
          <div class="banners__additional">
            <div class="banners__additional-texts">
              <p class="banners__after">{{ after }}</p>
              <div class="banners__quantity">
                <p>{{ quantity }}</p>
                <span>{{ quantity_info }}</span>
              </div>
            </div>
          </div>
        </div>
        <!-- <div class="banners__image">
          <img :src="image" :alt="title" />
        </div> -->
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Banners',
  components: {},
  data() {
    return {
      title:
        ' <span> </span>    ',
      speaker: '    ',
      image: 'img/speaker.png',
      subtitle:
        '3 ,       ',
      date: '22 , 20:00',
      offer: '  .  .',
      button: ' ',
      after: '  :',
      quantity: '60',
      quantity_info: '  ,   ',
      link: '#contact-us',
      advantages: [
        {
          text: '      ;',
        },
        {
          text: '   ,   ;',
        },
        {
          text: '  3      .',
        },
      ],
    }
  },
  computed: {},
  mounted() {},
  methods: {
    async goTo(link) {
      if (!this.$scrollTo(link)) {
        setTimeout(() => {
          this.$scrollTo(link)
        }, 500)
      }
    },
  },
  mounted() {},
  beforeDestroy() {},
}
</script>
